<template>
  <transition name="slide">
    <div class="list">
      <header>
        <section class="top-title">
          <a href="javascript:;" class="back" @click="back()">
            <img
              src="data:image/png;base64,"
              alt
            >
          </a>
          <h1 class="title">频道管理</h1>
        </section>
      </header>
      <div class="content">
        <p class="type-title type-title-1">
          <span class="left">
            我的频道
            <span class="info">(长按拖动排序)</span>
          </span>
          <!-- <span class="right" @click="edit()">{{crossText}}</span> -->
          <!-- <span class="addType">保存</span> -->
        </p>
        <ul class="selectList" id="selectList">
          <!-- <li class="first">看点</li> -->
          <li
            :class="[item=='看点'?'up-item':'up-item']"
            :id="index"
            v-for="(item,index) of this.selectList"
            @click="deleteType(index)"
            :key="index"
          >
            <template lang="html" v-if="item=='看点'">推荐</template>
            <template lang="html" v-else>{{item}}</template>
            <img :src="imgsrc" alt class="delete" v-show="cross">
          </li>
        </ul>
        <!-- <p class="type-title type-title-2">
          推荐频道
          <span class="info">(点击添加频道)</span>
        </p> -->
        <ul class="toBeList">
          <li
            class="up-item up-item2"
            :id="index"
            v-for="(item,index) of toBeList"
            @click="addType(index)"
            :key="index"
          >
            <template lang="html" v-if="item=='看点'">推荐</template>
            <template lang="html" v-else>{{item}}</template>
          </li>
        </ul>
      </div>
    </div>
  </transition>
</template>

<script type="text/javascript">
// import sortablejs from "sortablejs";
import bus from "../../assets/bus/bus.js";
// import {mapActions} from 'vuex'
export default {
  name: "Home",
  data() {
    return {
      selectList: [], //选中的列表
      toBeList: [], //待选列表
      list: [], //传回父元素的数据
      imgsrc: require("../../assets/image/shanchu.png"),
      cross: false, //删除键的显示
      crossText: "编辑"
    };
  },
  created() {
    //进入到list页面的时候,获取vuex的数据
    this.selectList = this.$store.state.selectList;
  },
  methods: {
    back() {
      this.$router.back();
      // this.$store.state.newList = this.selectList
    },
    // ...mapActions([
    // 	"deleteType",
    // 	"addType"
    // ])

    //删除频道
    deleteType(index) {
      // this.toBeList.push(this.selectList[index]);
      // this.selectList.splice(index,1);
      // //测试
      // this.$store.commit("deleteType", index);
      // localStorage.setItem("localSelectList", JSON.stringify(this.selectList)); //把前十条的栏目入缓存
    },
    //增加频道
    addType(index) {
      // this.selectList.push(this.toBeList[index]);
      // this.toBeList.splice(index,1);
      //测试
      // this.$store.commit("addType", index);
      // localStorage.setItem("localSelectList", JSON.stringify(this.selectList)); //把前十条的栏目入缓存
      // localStorage.setItem("List", true);
    },
    removeByValue(arr, val) {
      //去除指定的数组
      for (let i = 0; i < arr.length; i++) {
        if (arr[i] == val) {
          arr.splice(i, 1);
          break;
        }
      }
    },
    edit() {
      this.cross = !this.cross;
      if (this.cross == true) {
        this.crossText = "完成";
      } else if (this.cross == false) {
        this.crossText = "编辑";
      }
    }
  },
  mounted() {
    var that = this;
    //因为进入栏目路由的话就不需要请求数据了
    if (localStorage.getItem("localSelectLists")) {
      // console.log('有缓存')
      // this.$store.commit('getSelectList',JSON.parse(localStorage.getItem('localSelectList')))//提交数据到vuex
      let noList = JSON.parse(localStorage.getItem("localSelectLists"));
      if (that.selectList.length > 0) {
        for (let j = 0; j < that.selectList.length; j++) {
          that.removeByValue(noList, that.selectList[j]);
        }
        that.toBeList = noList;
        // that.$store.commit("getNoSelectList", noList); //提交数据到vuex
      }
    }
    // this.$ajax({
    // 	url: '/a/news.list',
    // 	method: 'get'
    // }).then(res => {
    // 		if(that.selectList.length > 0) {
    // 			for(let j = 0; j < that.selectList.length;j++) {
    // 				that.removeByValue(res.list,that.selectList[j])
    // 			}
    // 			that.toBeList = res.list
    // 			that.$store.commit('getNoSelectList',res.list)//提交数据到vuex
    //
    // 		}
    // });
    // var el = document.querySelector("#selectList");
    // const sortable = new sortablejs(el, {
    //   animation: 200,
    //   delay: 0,
    //   draggable: ".up-item",
    //   preventOnFilter: true,
    //   scrollSpeed: 10,
    //   onUpdate: function(evt) {
    //     var items = document.querySelectorAll(".selectList>li");
    //     that.list = [];
    //     for (let i = 0; i < items.length; i++) {
    //       that.list.push(items[i].innerText);
    //     }
    //     console.log(that.list);
    //     let oldArr = that.list;
    //     // let filterArr = oldArr.map((v, i) => {
    //     //   v == "推荐" ? (v = "看点") : v;
    //     //   return v;
    //     // });
    //     console.log(oldArr);
    //     that.$store.state.selectList = oldArr;
    //     that.$store.commit("getSelectList", oldArr); //提交数据到vuex
    //   }
    // });
  }
};
</script>

<style lang="scss" scoped="">
@import "@/assets/styles/common.scss";
.slide-enter-active,
.slide-leave-active {
  transition: all 0.3s;
}
.slide-enter,
.slide-leave-to {
  transform: translate3d(100%, 0, 0);
}
.list {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 10000;
  margin-top: -37px;
  max-width: 750px;
  header {
    position: fixed;
    top: 0;
    z-index: 2;
    width: px2rem(375px);
    overflow: hidden;
    .top-title {
      position: relative;
      width: 100%;
      height: px2rem(44px);
      background-color: #d43d3d;
      color: #fff;
      .back {
        position: absolute;
        left: 0;
        top: 0;
        width: px2rem(50px);
        height: px2rem(44px);
        img {
          width: px2rem(20px);
          height: px2rem(20px);
          margin-left: px2rem(15px);
          margin-top: px2rem(12px);
          -webkit-transform: scale(1);
          transform: scale(1);
        }
      }
      .title {
        position: relative;
        display: block;
        width: px2rem(150px);
        margin: 0 auto;
        text-align: center;
        height: 100%;
        line-height: px2rem(44px);
        font-weight: 700;
        font-size: px2rem(22px);
        color: #fff;
      }
    }
  }
  .content {
    overflow: hidden;
    position: relative;
    width: 100%;
    margin-top: px2rem(44px);
    .type-title {
      width: 100%;
      height: px2rem(40px);
      font-size: px2rem(16px);
      line-height: px2rem(40px);
      padding: 0 px2rem(10px);
      box-sizing: border-box;
      .info {
        color: #999;
        font-size: px2rem(12px);
      }
      .right {
        float: right;
        color: #d43d3d;
      }
    }
    .selectList,
    .toBeList {
      padding: 0 px2rem(10px);
      box-sizing: border-box;
      overflow: hidden;
      .up-item,
      .first {
        width: px2rem(51px);
        height: px2rem(34px);
        line-height: px2rem(34px);
        text-align: center;
        float: left;
        box-sizing: border-box;
        border: 1px solid #ddd;
        margin: px2rem(10px);
        cursor: pointer;
        font-size: px2rem(12px);
        border-radius: 5px;
        position: relative;
        .delete {
          width: 25%;
          position: absolute;
          right: -1px;
          top: -1px;
        }
      }
      .first {
        background-color: #ddd;
        border: none;
      }
    }
  }
}
// .sortable-chosen {
// 	border: 1px dashed #d43d3d !important;
// 	// display: none;
// 	// background: red;
// }
// .sortable-drag {
// 	border: 1px solid #ddd !important;
// }
</style>



